.toggle {
  cursor: pointer;
  margin: 0; }
  .toggle input[type=checkbox],
  .toggle input[type=radio] {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0; }

.toggle .handle {
  position: relative;
  top: 0;
  left: 0;
  display: inline-block;
  width: 40px;
  height: 10px;
  border-radius: 10px;
  background-color: #999; }
  .toggle .handle:before {
    position: absolute;
    top: -5px;
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
    left: 0;
    display: block;
    width: 20px;
    height: 20px;
    content: "";
    background-color: #ccc;
    transition: all 0.25s ease-in-out;
    border-radius: 50%; }

.toggle input[type=checkbox]:checked + .handle {
  background-color: #1b66aa; }
  .toggle input[type=checkbox]:checked + .handle:before {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
    background-color: #4f9be3; }

.toggle.is-mini .handle {
  background-color: #e37874;
  width: 32px;
  height: 16px; }
  .toggle.is-mini .handle::before {
    top: 0;
    background-color: #eee;
    border: 2px solid #999;
    width: 16px;
    height: 16px; }

.toggle.is-mini input[type=checkbox]:checked + .handle {
  background-color: #a4e276; }
  .toggle.is-mini input[type=checkbox]:checked + .handle:before {
    -webkit-transform: translateX(110%);
            transform: translateX(110%);
    background-color: #eee; }

.toggle.has-text .handle {
  width: 150px;
  text-align: center;
  height: 2rem;
  padding: 0.5em;
  color: #fff; }
  .toggle.has-text .handle:before {
    content: "";
    background-color: #fff;
    border: 1px solid #aaa;
    top: 0;
    width: 10px;
    height: 100%;
    border-radius: 10px;
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  .toggle.has-text .handle:after {
    content: attr(data-off);
    color: #fff;
    position: absolute;
    left: 50%;
    width: 100%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }

.toggle.has-text input[type=checkbox]:checked + .handle {
  background: #37c235; }
  .toggle.has-text input[type=checkbox]:checked + .handle:before {
    background-color: #fff;
    -webkit-transform: translate(140px);
            transform: translate(140px); }
  .toggle.has-text input[type=checkbox]:checked + .handle:after {
    content: attr(data-on); }

